.html-card .html-container {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}
.html-card .html-container .editor-pane,
.html-card .html-container .preview-pane {
  flex: 1;
  min-width: 600px;
  display: flex;
  flex-direction: column;
}
.html-card .html-container .quill-editor {
  height: 600px;
  border: 1px solid #f0f0f0;
  border-radius: 8px;
}
.html-card .html-container .quill-editor .ql-container {
  height: 600px;
}
.html-card .html-container .html-preview {
  height: 600px;
  overflow-y: auto;
  padding: 12px;
  border: 1px solid #f0f0f0;
  border-radius: 8px;
  background: #fff;
}
.html-card .html-container .button-group {
  margin-top: 12px;
}
@media (max-width: 768px) {
  .html-card .editor-pane,
  .html-card .preview-pane {
    min-width: 100%;
  }
  .html-card .quill-editor,
  .html-card .html-preview {
    height: 400px;
  }
}
